import { PropertyType } from '../../core/models/marker.model';

export function getEstimatedPriceText(propertyType: PropertyType): string {
  return propertyType === 'MFH' ? 'Schätzwerte' : 'Schätzwert';
}

export function getSalePriceText(propertyType: PropertyType): string {
  return propertyType === 'MFH' ? 'Kaufpreise' : 'Kaufpreis';
}
